/* Internas */
.texto {
  margin: 6rem;
}

.texto .titulo_pag {
  /*Colocamos las caracteristicas que tendra el titulo de nuestra pagina*/
  text-align: center;
  padding: auto;
  color: #967e76;
}

.secciones {
  /*Caracteristicas de las secciones de pago*/
  width: 100%;
  display: flex;
  flex-wrap: wrap; /*Los elementos podran estar en varias lineas*/
  justify-content: center; /*Contenido centrado*/
  text-align: center;
  padding-top: 4rem;
  padding-bottom: 4rem;
  background-color: #d7c0ae;
}

.secciones .seccion {
  /*Caracteristicas que tendra cada una de las secciones de pago*/
  width: 350px;
  height: 800px;
  border-radius: 8px; /*que tan redondeado sera el borde*/
  box-shadow: 2px 8px 8px rgba(0, 0, 0, 0.8); /*La densidad del sombreado*/
  overflow: hidden; /*Se levantara cuando el cursor pase sobre alguna de las secciones*/
  margin: 20px;
  transition: all 0.25s; /*Velocidad en que el levantamiento se realizara*/
  background-color: #fff;
  padding: 1.1rem;
}

.secciones .seccion:hover {
  transform: translateY(-15px); /*como se relizara la transición de levantamiento*/
  box-shadow: 0 12px 16px rgba(0, 0, 0, 0.2);
}

.secciones .seccion img {
  /*caracteristicas de la imagen*/
  height: 100px;
}

.secciones .seccion .titulo {
  /*Para el titulo de cada sección*/
  font-weight: 600;
  text-align: center;
}

.secciones .seccion .seccion_texto {
  /*caracteristicas del texto dentro de cada cuadro*/
  text-align: left;
  padding: 0 1rem;
  font-size: medium;
  cursor: pointer;
  height: 40rem;
}

.secciones .seccion .pago {
  margin: 0.5px;
  text-align: right;
  padding-right: 1.2rem;
}

.secciones .seccion .botones {
  /*Caracteristica de los botones*/
  margin: 0 auto;
  padding: 1rem;
  transition: all 0.2s; /*duración del levantamiento cuando pase el cursor*/
  font-size: 15px;
  width: 250px;
  height: 50px;
  background-color: #eeb284;
  border: #fff;
  border-radius: 8px;
  overflow: hidden; /*Se levantara cuando pase el cursor sobre el*/
  cursor: pointer;
}

