.container-form .btn-enviar:hover {
  background: #e39403;
}

.contenedor-seleccion-pago {
  width: 90%;
  max-width: 900px;
  margin: auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.tabla {
  width: 33%;
  height: 550px;
  margin: auto;
  border: 1px solid #967e76;
  border-radius: 4px;
  box-shadow: 0px 0px 4px 0px #967e76;
  padding: 60px;
  text-align: center;
  flex-grow: 1;
  color: #000;
}

.tabla > h2 {
  font-size: 30px;
  margin-bottom: 35px;
}

.tabla img {
  width: 64px;
  margin-bottom: 35px;
}

.tabla > h3 {
  font-size: 30px;
  margin-bottom: 20px;
}

.tabla > p {
  display: block;
  margin-bottom: 20px;
}

.tabla:hover {
  background: #b7c4cf;
  color: #000;
  transition: 0.5s all;
  transform: scale(1.1);
  cursor: pointer;
